/*
==========================ИНСТРУКЦИЯ! : ===========================================
чтобы стили преминялись у Вас задайте в html на место вашей кнопки такую конструкцию: 

    <a class="next">
        <svg class="next-svg">
            <use href="./images/sprite.svg#next"></use>
        </svg>
    </a>
*/
.next-svg {
  width: 12px;
  height: 10px;
  fill: $main-title-color;
  //предполагаемые атрибуты SVG замерял на макете линейками.
}
.next {
  width: 40px;
  height: 40px;
  background-color: $primary-white-color;
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  //в макете ховера по данной кнопке нет. Скорее всего ее забыли добавить. Нужно согласовать с Ментором.
  transition-property: background-color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    background-color: $hover-color;
  }
}
